.flexContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;

    width: 80%;
    border: yellow dashed 2px;
}


@media (max-width: 1280px) {
    .flexContainer{
        flex-direction: column;
        min-width: 100%;
        max-width: 100%;
        width: 100%;
    }

}